<template>
  <div>
    {{text}}
  </div>
</template>

<script>
import {refreshPay} from 'api/pay'
export default {
  data () {
    return {
      text: ''
    }
  },
  created() {
    console.log('created')
    refreshPay()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



